"use client"; // Asegura que este componente maneja eventos en el cliente
import { useState } from "react";
import { useFormState } from "react-dom";
import { redirect } from "next/navigation";
import api from "../../../api";

export default function ResumePage() {
  // Obtener los datos dinámicamente dentro del componente
  const [user, setUser] = useState(null);
  const [messages, setMessages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Cargar datos al montar el componente
  useState(() => {
    async function fetchData() {
      try {
        const fetchedUser = await api.user.fetch();
        const fetchedMessages = await api.message.list();
        setUser(fetchedUser);
        setMessages(fetchedMessages);
      } catch (err) {
        setError("Error al cargar los datos");
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, []);

  // Función para manejar el envío del mensaje
  async function add(formData) {
    "use server";

    try {
      const message = formData.get("text");
      if (!message.trim()) {
        throw new Error("El mensaje no puede estar vacío");
      }

      console.log("Mensaje recibido:", message);
      const url = await api.message.submit(message);

      if (!url) {
        throw new Error("URL no generada correctamente");
      }

      console.log("Redirigiendo a:", url);
      return Response.redirect(url);
    } catch (error) {
      console.error("Error en add():", error);
      return { error: error.message };
    }
  }

  // Estado para el manejo del formulario
  const [formState, formAction] = useFormState(add, { error: null });

  if (loading) return <p>Cargando...</p>;
  if (error) return <p>Error: {error}</p>;

  return (
    <section className="grid gap-8">
      {/* Si el usuario ya autorizó la integración, mostramos el formulario */}
      {user?.marketplace ? (
        <form action={formAction} className="grid gap-2">
          <textarea
            className="border-2 border-blue-400 p-2"
            name="text"
            placeholder="Hola, escribe tu mensaje..."
            rows={3}
          />
          <button className="rounded bg-blue-400 p-2" type="submit">
            Enviar
          </button>
          {formState?.error && (
            <p className="text-red-500">{formState.error}</p>
          )}
        </form>
      ) : (
        // Si no autorizó la integración, mostramos un botón para redirigirlo a Mercado Pago a autorizar
        <a
          className="rounded bg-blue-400 p-2 text-center"
          href={user?.authorizationUrl}
        >
          Conectar Mercado Pago
        </a>
      )}
      <ul className="grid gap-2">
        {messages.map((message) => (
          <li key={message.id} className="rounded bg-blue-400/10 p-4">
            {message.text}
          </li>
        ))}
      </ul>
    </section>
  );
}
